.bonus {

}

.bonus__main {
	position : relative;
}

.bonus__wrapper {
	background-image      : url("../images/bonus/bg-mob.png");
	background-repeat     : no-repeat;
	background-size       : cover;
	border                : 1px solid #EC17A5;
	box-shadow            : 0 0 4px 0 rgba(0, 0, 0, 0.25);
	border-radius         : 20px;
	padding               : 13px 13px 0 13px;
	display               : grid;
	grid-template-columns : repeat(4, 1fr);
	align-items           : center;
	margin-bottom         : 20px;

	@media screen and (min-width : $break-desktop) {
		grid-template-columns : repeat(12, 1fr);
		background-image      : url("../images/bonus/bg-desktop.png");
		margin-bottom         : 68px;
	}

}

.bonus__logo {
	grid-column      : 4 span;
	border           : 1px solid #EC17A5;
	border-radius    : 30px;
	text-align       : center;
	background-color : var(--white-color);
	min-width        : 259px;
	margin           : 0 auto 24px auto;
	padding          : 18px 47px;
	@media screen and (min-width : $break-desktop) {
		grid-column   : 3 span;
		margin-top    : -13px;
		min-width     : 208px;
		margin-bottom : 0;
		margin-left   : 0;
		padding       : 33px 25px;
	}
}

.bonus__wrap {
	grid-column           : 4 span;
	display               : grid;
	grid-template-columns : subgrid;
	align-items           : center;
	max-width             : 300px;
	grid-column-gap       : 25px;
	@media screen and (min-width : $break-desktop) {
		grid-column  : 9 span;
		grid-row-gap : 10px;
	}

	.b-btn--default {
		grid-column : 4 span;
		min-width   : 259px;
		margin      : 28px auto 16px auto;
		padding     : 13px 13px;
		@media screen and (min-width : $break-desktop) {
			grid-column : 7/10;
			margin      : 0 auto;
			padding     : 19px 13px;
			min-width   : 184px;
		}
	}
}

.bonus-item {
	display        : flex;
	flex-direction : column;
	align-items    : center;
	height         : 100%;


	&:nth-child(1) {
		grid-column : 2 span;

		@media screen and (min-width : $break-desktop) {
			grid-column : 1/4;
		}
	}

	&:nth-child(2) {
		grid-column : 2 span;

		@media screen and (min-width : $break-desktop) {
			grid-column : 4/7;
		}

	}
}

.bonus-item__wrap {
	border           : 1px solid #EC17A5;
	border-radius    : 30px;
	padding          : 15px 6px;
	background-color : var(--white-color);
	width            : 100%;
	@media screen and (min-width : $break-desktop) {
		padding : 12px 27px;
		width   : auto;
	}
}

.bonus-item__name {
	font-weight      : 400;
	font-size        : 16px;
	text-align       : center;
	margin-bottom    : 20px;
	display          : none;
	color            : #ED1C9E;
	background-color : var(--white-color);
	border           : 1px solid #EC17A5;
	border-radius    : 30px;
	padding          : 0 13px;
	@media screen and (min-width : $break-desktop) {
		display : block;
	}
}

.bonus-item__text {
	text-align  : center;
	font-weight : 700;
	font-size   : 14px;
	color       : #1F1F1F;

	@media screen and (min-width : $break-desktop) {
		font-size : 16px;
	}
}

.bonus-item__about {
	text-align  : center;
	font-weight : 700;
	color       : var(--gold-color);
	font-size   : 14px;

	@media screen and (min-width : $break-desktop) {
		font-size : 16px;
	}
}

.bonus-item__list {
	list-style      : none;
	display         : flex;
	padding         : 0;
	margin          : auto 0 0 0;
	justify-content : center;
}

.bonus__text {
	font-weight : 400;
	font-size   : 10px;
	text-align  : center;
	color       : #1F1F1F;
	grid-column : 4 span;
	line-height : 120%;
	@media screen and (min-width : $break-desktop) {
		grid-column : 1/10;
		font-size   : 12px;
	}
}

.casino-text__wrapper {
	padding       : 14px 8px;
	margin-bottom : 55px;

	@media screen and (min-width : $break-desktop) {
		padding       : 9px 33px;
		margin-bottom : 160px;
	}

	div {
		font-weight : 400;
		font-size   : 16px;
		color       : #1B1B1B;
		text-align  : center;

		&:not(:last-child) {
			margin-bottom : 15px;
		}
	}
}

.box-default {
	background    : #FBCCFA;
	border        : 1px solid #EC17A5;
	border-radius : 30px;
	overflow      : hidden;
}

.section-title {
	font-weight : 700;
	font-size   : 14px;
	text-align  : center;
	color       : #92268B;

	@media screen and (min-width : $break-desktop) {
		font-size : 20px;
	}
}

.text {
	font-weight : 400;
	font-size   : 14px;
	color       : #1B1B1B;

	@media screen and (min-width : $break-desktop) {
		font-size : 16px;
	}
}
