.hero {
	background-image      : url("../images/hero/hero-bg.jpg");
	background-repeat     : no-repeat;
	background-position-x : center;
	padding               : 10px 0 20px 0;
	margin-bottom         : 20px;

	@media screen and (min-width : $break-desktop) {
		padding         : 35px 0 45px 0;
		background-size : cover;
		margin-bottom   : 68px;
	}

}

.hero__wrapper {
	display        : flex;
	flex-direction : column;
	align-items    : center;
}

.hero__img {
	margin-bottom : 13px;
	width         : 64px;

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 21px;
		width         : 138px;
	}
}

.hero__title {
	font-weight   : 700;
	font-size     : 24px;
	text-align    : center;
	color         : var(--white-color);
	margin-bottom : 8px;

	@media screen and (min-width : $break-desktop) {
		font-size     : 46px;
		margin-bottom : 20px;
	}
}

.hero__list {
	display         : flex;
	justify-content : space-between;
	box-shadow      : 0 0 8px 0 var(--white-color);
	background      : var(--white-color);
	padding         : 5px 10px;
	list-style      : none;
	margin          : 0;
	border-radius   : 20px;
	gap             : 14px;
	@media screen and (min-width : $break-desktop) {
		gap : 27px;
	}
}

.hero__item {
	font-weight : 400;
	font-size   : 9px;
	text-align  : center;
	color       : #323232;

	@media screen and (min-width : $break-desktop) {
		font-size : 16px;
	}
}

.hero__text {
	font-weight   : 400;
	font-size     : 14px;
	color         : var(--white-color);
	text-align    : center;
	margin-bottom : 11px;
	@media screen and (min-width : $break-desktop) {
		font-size     : 20px;
		margin-bottom : 37px;
	}
}
