.about {
	margin-bottom : 20px;

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 68px;
	}
}

.about__container {
	position : relative;
}

.about__wrapper {
	overflow : visible;
}

.about__img {
	text-align : center;
	margin-top : -30px;

	img {
		width : 280px;

		@media screen and (min-width : $break-desktop) {
			width : 550px;
		}
	}

	@media screen and (min-width : $break-desktop) {
		margin-top : -90px;
	}
}

.about__main {
	padding : 10px 11px;

	@media screen and (min-width : $break-desktop) {
		padding : 27px 19px;
	}
}

.about__top {
	margin-bottom : 27px;

	@media screen and (min-width : $break-desktop) {
		display         : flex;
		align-items     : flex-start;
		justify-content : space-between;
	}
}

.about__left {
	margin-bottom : 22px;
	@media screen and (min-width : $break-desktop) {
		max-width     : 470px;
		margin-bottom : 0;
	}
}

.about__name {
	font-weight   : 700;
	font-size     : 14px;
	text-align    : center;
	color         : #92268B;
	margin-bottom : 21px;
	line-height   : 120%;

	@media screen and (min-width : $break-desktop) {
		font-size     : 16px;
		margin-bottom : 28px;
	}
}

.about__wrap {

	@media screen and (min-width : $break-desktop) {
		max-width : 381px;
	}
}

.about__text {
	font-weight : 400;
	font-size   : 14px;
	color       : #1B1B1B;

	@media screen and (min-width : $break-desktop) {
		font-size : 16px;
	}

	&:not(:last-child) {
		margin-bottom : 20px;
	}
}

.about__txt {
	font-weight : 700;
	font-size   : 14px;
	color       : #92268B;
	line-height : 120%;

	@media screen and (min-width : $break-desktop) {
		font-size : 16px;
	}
}

.about__list {
	list-style : none;
	padding    : 0;
	margin     : 0 0 30px 0;

	li {
		div {
			font-weight : 700;
			font-size   : 14px;
			color       : #92268B;
			line-height : 120%;
			@media screen and (min-width : $break-desktop) {
				font-size : 16px;
			}
		}

		span {
			font-weight : 400;
			font-size   : 14px;
			color       : #1B1B1B;
			line-height : 120%;
			@media screen and (min-width : $break-desktop) {
				font-size : 16px;
			}
		}

		&:not(:last-child) {
			margin-bottom : 5px;
			@media screen and (min-width : $break-desktop) {
				margin-bottom : 25px;
			}
		}
	}
}

.about__items {
	display        : flex;
	flex-direction : column;
	align-items    : center;
	gap            : 50px;
	@media screen and (min-width : $break-desktop) {
		justify-content : space-between;
		flex-direction  : row;
		gap             : 17px;
	}
}


.about-item {
	display        : flex;
	flex-direction : column;
	align-items    : center;
	width          : 100%;
	padding        : 0 20px;
	@media screen and (min-width : $break-desktop) {
		padding : 0;
	}

	&__img {
		margin-bottom : 22px;

		@media screen and (min-width : $break-desktop) {
			margin-bottom : 26px;
		}

		img {
			width      : 100%;
			height     : 100%;
			object-fit : cover;
		}
	}

	.b-btn--default {
		padding   : 5px 17px;
		min-width : 212px;
		@media screen and (min-width : $break-desktop) {
			min-width : 185px;
		}

		span {
			display     : block;
			white-space : normal;
			max-width   : 135px;
			@media screen and (min-width : $break-desktop) {

			}
		}
	}
}
