.play {
	margin-bottom : 69px;
	@media screen and (min-width : $break-desktop) {
		margin-bottom : 124px;
	}
}

.play__wrapper {
	padding : 14px 14px;
}

.play__main {
	position : relative;

	.img-bg--about {
		top : 400px;
	}

	.img-bg--bottom {
		bottom : -260px;
		left   : -370px;
	}
}

.play__item {
	&:not(:last-child) {
		margin-bottom : 20px;
	}
}

.play__title {
	color : #1B1B1B;
}

.play__text {
	&:not(:last-child) {
		margin-bottom : 20px;
	}
}
