@keyframes mmslideIn {
	from {
		opacity : 0;
	}
	to {
		opacity : 1;
	}
}

@keyframes mmslideOut {
	from {
		opacity : 1;
	}
	to {
		opacity : 0;
	}
}

@keyframes mmslideInBg {
	from {
		opacity : 0;
	}
	to {
		opacity : .7;
	}
}

@keyframes mmslideOutBg {
	from {
		opacity : .7;
	}
	to {
		opacity : 0;
	}
}

.modal {
	display : none;
}

.modal.is-open {
	display : block;
}

.modal[aria-hidden="false"] .modal__overlay {
	animation : mmslideIn .3s linear;
}

.modal[aria-hidden="true"] .modal__overlay {
	animation : mmslideOut .3s linear;
	@media screen and (min-width : $break-table) {

	}
}

.modal__overlay {
	position                   : fixed;
	z-index                    : 8888;
	top                        : 0;
	right                      : 0;
	bottom                     : 0;
	left                       : 0;
	display                    : flex;
	overflow-y                 : auto;
	align-items                : normal;
	flex-flow                  : row nowrap;
	justify-content            : normal;
	box-sizing                 : border-box;
	padding                    : 8px;
	background                 : rgba(#000, .5);
	-webkit-overflow-scrolling : touch;
	overscroll-behavior        : contain;
	backdrop-filter            : blur(10px);

	_:-ms-fullscreen, :root & {
		display : block;
	}

	@media screen and (min-width : $break-table) {
		padding : 20px;
	}
	@media screen and (min-width : $break-desktop) {

	}

	&--news {
		@media screen and (max-width : 680px) {
			padding : 0;
		}
	}

	.overflowscrolling & {
		overflow-y : scroll;
	}
}

.modal__container {
	position        : relative;
	box-sizing      : border-box;
	width           : 540px;
	max-width       : 100%;
	margin          : auto;
	padding         : 16px 16px;
	border-radius   : 24px;
	border          : 1px solid #EC17A5;
	background      : #FBCCFA;
	background-size : cover;
	z-index         : 9999;
	box-shadow      : 0 0 4px 0 rgba(0, 0, 0, 0.25);

	@media screen and (min-width : 1024px) {
		padding : 59px 46px 40px 46px;
	}
	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {

	}
}

.modal__content {

}

.modal__close {
	font-size                   : 18px;
	position                    : absolute;
	z-index                     : 300;
	top                         : 0;
	right                       : 0;
	box-sizing                  : border-box;
	width                       : 62px;
	height                      : 62px;
	padding                     : 0;
	cursor                      : pointer;
	transition                  : opacity .3s ease;
	opacity                     : 1;
	border                      : 0;
	outline                     : 0;
	background                  : transparent;
	-webkit-tap-highlight-color : transparent;

	&:after {
		content       : "";
		position      : absolute;
		z-index       : 1;
		top           : 50%;
		left          : 50%;
		width         : 32px;
		height        : 32px;
		transform     : translate(-50%, -50%);
		border-radius : 50%;
	}

	&:before {
		content    : "";
		position   : absolute;
		z-index    : 10;
		top        : 50%;
		left       : 50%;
		width      : 16px;
		height     : 16px;
		transform  : translate(-50%, -50%);
		background : url("../images/close-min.svg") no-repeat 50% 50%;
	}

	&:hover {
		opacity : .7;
	}
}


.system-popup__text {
	color       : #1B1B1B;
	font-weight : 400;
	font-size   : 20px;
	text-align  : center;
	margin      : 0 auto 41px auto;

}

.system-popup__btn {
	display        : flex;
	flex-direction : column;
	align-items    : center;
	gap            : 10px;

	@media screen and (min-width : $break-desktop) {
		flex-direction  : row;
		justify-content : center;
		gap             : 60px;
	}

	.b-btn--default {
		min-width : 184px;
	}
}
