.modal__overlay {
  position: fixed;
  z-index: 8888;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  overflow-y: auto;
  align-items: normal;
  flex-flow: row nowrap;
  justify-content: normal;
  box-sizing: border-box;
  padding: 8px;
  background: rgba(0, 0, 0, 0.5);
  -webkit-overflow-scrolling: touch;
  overscroll-behavior: contain;
  backdrop-filter: blur(10px);
}
.modal__overlay _:-ms-fullscreen, :root .modal__overlay {
  display: block;
}
