  overflow-y: scroll;
}

.modal__container {
  position: relative;
  box-sizing: border-box;
  width: 540px;
  max-width: 100%;
  margin: auto;
  padding: 16px 16px;
  border-radius: 24px;
  border: 1px solid #EC17A5;
  background: #FBCCFA;
