.modal__close {
  font-size: 18px;
  position: absolute;
  z-index: 300;
  top: 0;
  right: 0;
  box-sizing: border-box;
  width: 62px;
  height: 62px;
  padding: 0;
  cursor: pointer;
  transition: opacity 0.3s ease;
  opacity: 1;
  border: 0;
  outline: 0;
  background: transparent;
  -webkit-tap-highlight-color: transparent;
}
.modal__close:after {
  content: "";
  position: absolute;
  z-index: 1;
  top: 50%;
  left: 50%;
  width: 32px;
  height: 32px;
  transform: translate(-50%, -50%);
  border-radius: 50%;
}
.modal__close:before {
  content: "";
  position: absolute;
  z-index: 10;
  top: 50%;
  left: 50%;
  width: 16px;
  height: 16px;
  transform: translate(-50%, -50%);
  background: url("../images/close-min.svg") no-repeat 50% 50%;
}
