.page-wrapper {
	overflow : hidden;
}

.container {
	box-sizing : border-box;
	width      : (989 + 20) * 1px;
	max-width  : 100%;
	margin     : auto;
	padding    : 0 10px;

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {

	}
}

.img-bg {
	position : absolute;
	display  : none;
	z-index  : -1;

	@media screen and (min-width : $break-desktop) {
		display : block;
	}

	&--bonus {
		top  : -50px;
		left : -230px;
	}

	&--about {
		top   : 40px;
		right : -240px;
	}

	&--bottom {
		left   : -350px;
		bottom : -197px;
	}

	&--contacts {
		left : -220px;
		top  : -350px;
	}

	&--right {
		right  : -290px;
		bottom : -410px;
	}
}

