
.accordion {
	background-color : var(--white-color);
	padding          : 13px 17px;
	width            : 100%;
	border           : 1px solid #EC17A5;
	border-radius    : 20px;

	&:not(:last-child) {
		margin-bottom : 20px;
	}
}

.accordion__control {
	align-items      : start;
	background-color : transparent;
	border           : none;
	display          : flex;
	justify-content  : space-between;
	padding          : 0;
	width            : 100%;
	cursor           : pointer;

	@media screen and (min-width : $break-desktop) {
		align-items : center;
	}
}

.accordion.open .accordion__content {
	grid-template-rows : 1fr;
	margin-top         : 10px;
}

.accordion__content {
	display            : grid;
	grid-template-rows : 0fr;
	transition         : grid-template-rows .3s, margin-top .3s;
}

.accordion__main {
	overflow    : hidden;
	font-weight : 400;
	font-size   : 14px;
	color       : #92268B;

	@media screen and (min-width : $break-desktop) {
		font-size : 16px;
	}

	ul {
		max-width    : 628px;
		margin       : 0;
		padding-left : 20px;

		li {
			font-weight : 400;
			font-size   : 16px;
			line-height : 120%;
		}
	}
}

.accordion__icon {
	height      : 24px;
	margin-left : 22px;
	transform   : rotate(0deg);
	transition  : transform .3s ease;
}

.accordion.open .accordion__icon {
	transform  : rotate(180deg);
	transition : transform .3s ease;
}

.accordion__title {
	color       : #92268B;
	font-weight : 700;
	font-size   : 14px;
	text-align  : left;

	@media screen and (min-width : $break-desktop) {
		font-size : 16px;
	}
}


